<template>
  <div class="more">
    <h2>車輛詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">編輯</div>
	  </div>
	  <div class="more-c">
		<el-button type="primary" icon="el-icon-document-checked" @click="sub()">存檔</el-button>
        <div class="more-d">
		  <span>狀態: {{list.Status ? "可派發" : "不可派發"}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.Status" placeholder="請選擇">
              <el-option :label="'可派發'" :value="1"></el-option>
			  <el-option :label="'不可派發'" :value="0"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>出廠年月: {{Function.moment.yyyymmdd(list.MakeDate)}}</span>
		  <div class="more-d-edit" v-if="edit">
            <el-date-picker v-model="list.MakeDate" value-format="yyyy-MM" type="month"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>最後驗車: {{Function.moment.yyyymmdd(list.LastCheckDate)}}</span>
		  <div class="more-d-edit" v-if="edit">
			<el-date-picker v-model="list.LastCheckDate" value-format="yyyy-MM-dd" type="date"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車身顏色: {{list.CarColor}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.CarColor"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>類別: {{list.CarCategory[0].CarTypeName}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.CarCategory[0].Id">
              <el-option :label="'一般車'" :value="236"></el-option>
			  <el-option :label="'福祉車'" :value="214"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車牌號碼: {{list.CarNo}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.CarNo"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>廠牌型號: {{list.FactoryType}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.FactoryType"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛來源: {{list.CarFrom}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.CarFrom"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車頂高度: {{list.CarTop==225 ? "正常":"高頂"}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.CarTop">
              <el-option :label="'正常'" :value="225"></el-option>
			  <el-option :label="'高頂'" :value="224"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>輪椅數量: {{list.SeatNum}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input-number v-model="list.SeatNum" :min="0"></el-input-number>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>輪椅數量: {{list.WheelchairNum}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input-number v-model="list.WheelchairNum" :min="0"></el-input-number>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛保險:<el-tag v-for="(item,index) in list.CarSecure">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarSecure" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list2" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛設備:<el-tag v-for="(item,index) in list.CarDevice">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarDevice" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list1" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>適用駕照類型:<el-tag v-for="(item,index) in list.CarLicense">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarLicense" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list3" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  list_key:[],
	  edit:false,
	  all_type:"",
	  list1:"",
	  list2:"",
	  list3:"",
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_car_more(x)
	  this.all_type=await this.Function.get_CarsType_all()
	  this.list1=this.all_type.filter(re=>re.ParentID==7)
	  this.list2=this.all_type.filter(re=>re.ParentID==6)
	  this.list3=this.all_type.filter(re=>re.ParentID==9)
	},
	sub:async function() {
	  this.GLOBAL.show_black=true
      if(this.edit) {
        alert(await this.Function.put_car(this.list))
	  }
	  else {
        alert(await this.Function.post_car(this.list))
	  }
	  await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
	}
  }
}
</script>



