<template>
  <div class="dispatch">
    <h2>調度控制台</h2>
    <MBox :title="'訂單詳細資料'">
      <div class="dispatch-mbox">
		  <div>
		  <h3>訂單資料</h3>
        <div v-if="!isd">
          <el-button @click="show_c_time=!show_c_time">變更時間</el-button>
          <div v-if="show_c_time" style="padding: 10px 0;">
            <el-select placeholder="請選擇時間" v-model="c_time" style="width:78%;">
              <el-option v-for="(item,index) in time" :label="item" :value="item"></el-option>
            </el-select>
            <el-button style="width:20%;" @click="set_time()">確定</el-button>
          </div>
        </div>
        <div class="more-d" v-if="!isd"><span>訂單編號: {{c_order.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>訂單編號: {{c_order.OrderDetails.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>預約搭車時間: {{Function.moment.hhmm(c_order.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>預約搭車時間: {{Function.moment.hhmm(c_order.OrderDetails.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>是否回程: {{c_order.IsBack ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>是否回程: {{c_order.OrderDetails.IsBack ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>共乘: {{c_order.CanShared ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>共乘: {{c_order.OrderDetails.CanShared ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>輪椅類型: {{c_order.WheelchairId}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>輪椅類型: {{c_order.OrderDetails.WheelchairId}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>陪同人數: {{c_order.FamilyWith}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>陪同人數: {{c_order.OrderDetails.FamilyWith}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>起點: {{c_order.FromAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>起點: {{c_order.OrderDetails.FromAddr}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>迄點: {{c_order.ToAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>迄點: {{c_order.OrderDetails.ToAddr}}</span><hr></div>
	    </div>
		<div>
		  <h3>個案資料</h3>
		    <div class="more-d" v-if="!isd"><span>個案編號: {{c_order.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案編號: {{c_order.CaseUser.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>個案姓名: {{c_order.CaseUserName}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案姓名: {{c_order.CaseUser.Name}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>市話: {{c_order.CaseUserTel}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>市話: {{c_order.CaseUser.Tel}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>手機: {{c_order.CaseUserPhone}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>手機: {{c_order.CaseUser.Phone}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>身分證字號: {{c_order.CaseUserUID}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>身分證字號: {{c_order.CaseUser.UID}}</span><hr></div>
	    </div>
	  </div>
	</MBox>
    <div class="seach">
      <el-col :span="4">
        <el-date-picker v-model="day" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
        <el-button type="primary" icon="el-icon-document-checked" @click="put_data()">推播</el-button>
      </el-col>
    </div>
	<div class="dispatch-order">
	  <div class="dispatch-order-d" v-for="(item,index) in order" draggable=true @dragstart="dragStart($event,item)" @dblclick="isd=false;c_order=item;MBox.switch_open()">
      <div>{{item.SOrderNo}} ({{item.CarCategoryId==236 ? "一般車":"福祉車"}})</div>
      <div>{{item.CaseUserName}} ({{item.SubsidyMode ? "公費":"自費"}})</div>
      <div>{{item.CaseUserName}}</div>
      <div>起: {{item.FromAddr}}</div>
      <div>迄: {{item.ToAddr}}</div>
      <div>{{Function.moment.hhmm(item.ReservationDate)+" / 預估"+item.ExpectedMinute+"分鐘"}}</div>
      <div>{{item.CanShared ? "可共乘":"不可共乘"}}</div>
	  </div>
	</div>
	<div class="dispatch-d">
    <table border="1" class="dispatch-table">
      <tr>
        <th>車輛\時間</th>
        <th v-for="(item,index) in time">{{item}}</th>
      </tr>
      <tr v-for="(item,index) in car" @drop="post_des($event,item)" @dragover="dragover($event)" @dragleave.stop="dragleave($event)">
        <th>{{item.CarNo}}</th>
        <td v-for="(item2,index2) in time">
          <div class="dispatch-table-d" v-for="(item3,index3) in dispatch" v-if="item3.DespatchDetails[0].CarNo==item.CarNo&&item3.time==item2">
			<div class="dispatch-table-d-d" v-for="(item4,index4) in item3.DespatchDetails" @contextmenu="menu_open($event,item4)" @dblclick="isd=true;c_order=item4;MBox.switch_open()">
			  <div class="dispatch-table-d-d-d" style="font-weight: bold;">{{item4.CaseUser.Name}} ({{item4.OrderDetails.ExpectedMinute}}分鐘)</div>
			  <div class="dispatch-table-d-d-d">{{item4.OrderDetails.ToAddr}}</div>
			  <i class="el-icon-close dispatch-table-d-d-x" @click="del_des(item4)"></i>
			  <div class="dispatch-table-d-d-menu" v-if="item4.open">
			    <div class="dispatch-table-d-d-menu-d">推播此訂單</div>
				<div class="dispatch-table-d-d-menu-d">變更司機</div>
				<div class="dispatch-table-d-d-menu-d" @click="del_des(item4)">刪除訂單</div>
			  </div>
			</div>
		  </div>
        </td>
      </tr>
     </table>
	  <div class="dispatch-d-row" v-if="show_row">
		<div class="dispatch-d-row-d">車輛\時間</div>
	    <div class="dispatch-d-row-d" v-for="(item,index) in time">{{item}}</div>
	  </div>
	  <div class="dispatch-d-cell" v-if="show_cell">
	    <div class="dispatch-d-cell-d" v-for="(item,index) in car">{{item.CarNo}}</div>
	  </div>
	</div>
  </div>
</template>

<script>
import moment from "moment";
import MBox from './MBox'
export default {
  data() {
    return {
      time:this.GLOBAL.time_list, 
	    order: "",
	    c_order:"",
      dispatch: "",
      car: "",
      isd:false,
      day: moment().format("YYYY-MM-DD"),
      show_c_time:false,
      c_time:"",
	    show_row:false,
	    show_cell:false
    };
  },
  components: {MBox},
  mounted: async function() {
    this.GLOBAL.show_black = true;
    await this.all_ready();
    await this.get_data();
    var that=this
    document.querySelectorAll(".d1-r")[0].addEventListener("scroll",function() {
      var list=document.querySelectorAll(".dispatch-order")[0]
      if(this.scrollTop>=(263-80)) {
        list.style.top=this.scrollTop-(263-80)+"px"
        console.log(this.scrollTop)
      }
    }, false);
    this.GLOBAL.show_black = false;
  },
  watch: {
    async $route(to, from) {
      this.GLOBAL.show_black = true;
      await this.get_data();
      this.GLOBAL.show_black = false;
    }
  },
  methods: {
    get_data: async function(x) {
      this.GLOBAL.show_black=true
      this.order=await this.Function.get_dispatch_order(this.day)
	    this.dispatch=await this.Function.get_dispatch_allgroup(this.day)
	    this.car=await this.Function.get_dispatch_car().catch(e=>alert(e))
	    for(let i in this.dispatch) {
	      this.dispatch[i].time=moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm")
      }
	    for(let i in this.car) {
	      this.car[i].open=false
      }
      this.re_scroll()
      this.GLOBAL.show_black=false
	},
	show_c:function() {
      console.log(this.dispatch);
	},
	re_scroll:async function() {
	  var that=this
      var myScroll = new IScroll(document.querySelectorAll(".dispatch-d")[0], {
        scrollX: true,
		scrollY: true,
		probeType: 3,
        mouseWheel: false,
        disablePointer: true,
        disableTouch: false,
        disableMouse: false
	  })
	  myScroll.on('scroll', function () {
      if(this.x<=-205) {
		    that.show_cell=true
		    that.re_cell()
		  }
		  else {
		    that.show_cell=false
		  }
	  });
	},
	re_cell:function() {
	  var cell=document.querySelectorAll(".dispatch-d-cell-d")
	  var tr=document.querySelectorAll(".dispatch-table tr")
	  for(let i=0;i<cell.length;i++) {
		  cell[i].style.height=tr[i+1].offsetHeight+"px"
		  cell[i].style.lineHeight=tr[i+1].offsetHeight+"px"
	  }
	},
	menu_open:function(e,x) {
	  e.preventDefault();
	  if(x.open) {
		x.open=false
		this.$forceUpdate()
		return 0
	  }
      for (let i in this.dispatch) {
	    for(let j in this.dispatch[i].DespatchDetails) {
          this.dispatch[i].DespatchDetails[j].open=false
		} 
	  }
	  x.open=!x.open
	  this.$forceUpdate()
	},
	dragStart:function(e,x) {
      e.dataTransfer.setData("Text", JSON.stringify(x));
	},
	dragover:function(e) {
      e.preventDefault();
      e.currentTarget.classList.add("dispatch-table-tr-on")
  },
  dragleave:function(e) {
    e.preventDefault();
    e.currentTarget.classList.remove("dispatch-table-tr-on")   
  },
  del_des:async function(x) {
    console.log(x)
    this.GLOBAL.show_black=true
    var flag=await this.Function.del_Despatch(x.OrderDetails.Id)
    await this.get_data()
    this.GLOBAL.show_black=false
  },
  
	post_des:async function(e,x) {
    this.GLOBAL.show_black=true
    e.preventDefault();
    this.dragleave(e)
	  var data = JSON.parse(e.dataTransfer.getData("Text"))
	  var obj = {
		  "DriverInfoId": x.DriverId,
		  "CarId": x.Id,
	    "StartDate": moment(data.ReservationDate).format("YYYY-MM-DD HH:mm"),
		  "DespatchDetail": [
		    { "OrderDetailId": data.Id }
		  ]
	  }	
    await this.Function.post_Despatch(obj)
    await this.get_data()
    this.GLOBAL.show_black=false
	}
  }
}
</script>


