<template>
  <div v-if="list.OrderDetails&&show===1">
	  <div class="map" ref="map1"></div>
      <div>
        <span>預估時間:</span><span class="order-d-d-s">{{list.OrderDetails.ExpectedMinute}} 分鐘</span>
        <span>預估里程:</span><span class="order-d-d-s">{{list.OrderDetails.TotalMileage / 1000}} 公里</span>
      </div>
      <div class="order-d-d">
        <h3>{{list.OrderDetails.SOrderNo}}</h3>
        <div>{{list.OrderDetails.FromAddr}}</div>
        <i class="el-icon-bottom"></i>
        <div>{{list.OrderDetails.ToAddr}}</div>
        <el-steps>
          <el-step v-for="(item,index) in list.OrderStatusLog" :title="item.StatusChinese"></el-step>
        </el-steps>
        <div class="order-d-d-d">
          <div>車資總額</div>
          <div>${{list.OrderDetails.TotalAmt-list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>政府補助</div>
          <div>${{list.OrderDetails.TotalAmt-list.OrderDetails.SelfPayAmt-list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>陪同金額</div>
          <div>${{list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>實際應收</div>
          <div>${{list.OrderDetails.RealTotalAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>實際實收</div>
          <div>${{list.OrderDetails.ReceivedAmt}}</div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  props:["list"],
  data() {
    return {
    maps:[],
    show:1,
	}
  },
  mounted:async function() {
    this.show_map()
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    show_map:async function() {
        var dom=document.querySelectorAll(".map")
        this.maps[0]=this.Function.initMap(dom[0])
	      this.Function.calculateAndDisplayRoute(this.maps[0],this.list.OrderDetails.FromAddr,this.list.OrderDetails.ToAddr)
    }
  }
}
</script>



